@import "../../../scss/resources/resources";
.product {
  &-title {
    font-size: 32px;
    line-height: 43px;
    color: #030303;
    font-weight: 700;
    margin-bottom: 6px;
  }

  &-meta {
    font-size: 12px;
    line-height: 20px;
    color: #666666;
    margin-bottom: 17px;
  }

  &-price {
    font-size: 24px;
    line-height: 30px;
    color: $red;
    font-weight: 700;
    margin-bottom: 18px;

    &-previous {
      position: relative;
      font-size: 16px;
      margin-bottom: 18px;
      color: #888888;
      
       &:before {
        border-bottom: 1px solid #888888;
        position: absolute;
        content: "";
        width: 100%;
        height: 50%;
        transform: rotate(-7deg);
      }
    }
  }

  &-filter {
    font-size: 12px;
    line-height: 40px;
    margin-bottom: 4px;
    color: $black;
  }
}
