@charset "UTF-8";
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  box-sizing: border-box; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none;
  padding-left: 0; }

blockquote,
q {
  quotes: none; }

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

body {
  font-family: "OpenSans", sans-serif;
  position: relative;
  padding-bottom: 226px; }

.container {
  max-width: 1200px;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto; }

.d-flex {
  display: flex; }

.d-none {
  display: none; }

.fw-bold {
  font-weight: 700; }

.modal-backdrop {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  overflow: hidden;
  z-index: 1; }

.shop-category .category-item {
  box-shadow: 0 0 10px 5px #ababab; }

.listing-products .product-item {
  box-shadow: 0 0 5px 5px #ababab; }
  .listing-products .product-item:hover {
    box-shadow: 0 0 5px 2px #ff6008;
    background-color: #fff; }

.breadcrumbs-section {
  background-color: #cfcfcf;
  box-shadow: 0 2px 2px 1px #ababab; }

.main-search,
.subscribe-email,
.header-dropdown-login-details,
.login-form-input,
.checkout-delivery-content-input,
.checkout-delivery-method,
.checkout-delivery-details,
.checkout-product-item,
.checkout-payment-exp-input {
  border: 1px solid #000; }

.size-item {
  border: 2px solid #000; }

.header-dropdown-login-details {
  border-bottom: 0; }

.size-item.active, .product-counter-btn, .basket-item-delete, .login-cancel-btn {
  border-color: #000;
  background-color: rgba(0, 0, 0, 0.7);
  color: #FFF;
  font-weight: 700;
  opacity: 1; }

.header-dropdown-login-details-item,
.basket-item {
  border-bottom: 1px solid #000; }

.sub-page-sub-title,
.main-menu-link {
  color: #000; }

.main-menu-link {
  font-size: 16px; }

.order-history-item-dark {
  border: 2px solid #000; }

.breadcrumbs-section {
  display: none;
  background-color: #f3f3f3;
  padding: 17px 0 15px; }
  @media (min-width: 992px) {
    .breadcrumbs-section {
      display: block; } }

.breadcrumbs-list {
  display: flex; }
  .breadcrumbs-list li::after {
    content: '•';
    font-size: 13px;
    margin: 0 10px; }
  .breadcrumbs-list li:last-child::after {
    content: none; }

.breadcrumbs-link {
  font-size: 14px;
  line-height: 40px;
  color: #33333a;
  text-transform: uppercase; }
  .breadcrumbs-link:active, .breadcrumbs-link:hover {
    color: #ff6008; }
